import React, { useState, useEffect, useRef } from 'react';



import {
  _monthNames, 
  _dayShortNames, 
  renderCalendar,
  parseDateShort
} from '../stores/Utils';


import { SvgIcon_Cmpt } from '.';

import "./CalendarModalPicker.css";
import { CloseModal } from './DialogHRM';

const Icon2 = SvgIcon_Cmpt();

const activeColor = "var(--hrm_palette-blue-purple)";

const activeBckgrnColor = "#eff0ff";




const CalendarPicker = ({  data, onConfirm,  initValue, init, last, isRange}) => {


  const handleChange =  (v) => {
    onConfirm?.(v);
    data.modalID && CloseModal({id:data.modalID})
	}

  return (
      <DatePicker  
        onConfirm={handleChange}
        onCancel={()=>{
          data.modalID && CloseModal({id:data.modalID})
        }}
        initValue={initValue}
        init={init}
        last={last}
        isRange={isRange}
      />
  )
}






const DatePicker = ({ onConfirm, onCancel, init, last, isRange}) => {
  const [initDate, set_initDate] = useState(init);
  const [lastDate, set_lastDate] = useState(last);
  const [isLastDate, set_isLastDate] = useState(false);
  
	
  const updDate = (dd) =>{
    if(isRange){
      if(isLastDate && dd>initDate){
        set_lastDate(dd);
        set_isLastDate(false)
      }else{
        set_initDate(dd);
        set_lastDate(null);
        set_isLastDate(true)
      }
    }else{
      set_initDate(dd);
      set_lastDate(null);
      set_isLastDate(true)
    }
  }

   
  const handleOk = () =>{

    (isRange && initDate && lastDate ||  initDate )&& 
    onConfirm(
      {initDate,lastDate}
    );
  }

  return (
    <div className="dateBox" >
     
          <div className="date_header_box"  >
            <div  style={ styles.date_header_title}>
                {isRange?"Select Ranges":"Select Date"}
            </div>
          </div>
          <div className={"_dsplFlx"} style={styles.date_range_box}>
            <div  style={styles.date_range_title}>
                {initDate? parseDateShort(initDate) :"Start Date"}
            </div>
            {isRange?
            <div  style={styles.date_range_space_title}>
                {" - "}
            </div>:null}
             {isRange?
            <div  style={styles.date_range_title}>
              {lastDate?parseDateShort(lastDate) :"End Date"}
            </div>
            :null}
            <div className="flexSpace"/>
            {/*
            <Icon2 name={"lead-pencil"} color={"#444"}/>
            */}
          </div>
        
        <CalendarComponent 
          onChangeDate={updDate}
          init={initDate}
          last={lastDate}
        />
        <div className={"_dsplFlx"} style={styles.calendar_action_group}>
          <div className="flexSpace"/>
          <div
            onClick={()=>onCancel()}
            size={40}
            className={"_dsplFlx"}
          >
            
              <div  style={ styles.btn_cal_cancel}>
                {"Cancel"}
              </div>
          </div>
          <div
              onClick={handleOk}
              size={40}
          >
            <div
              className={"_dsplFlx"}
              style={{
                marginLeft: 16,
                marginRight: 8
              }}
            >
              <div  

                style={{
                  color: isRange &&  initDate && lastDate || initDate ? activeColor : "#8a8a8a",
                  fontSize: 15,
                  fontFamily:  "Google Sans,GoogleSans-Local",
                  fontWeight:700,
                  padding: 8
                }}
              >
                {"OK"}
              </div>
            </div>
          </div>
        </div>
    </div>
  )

}



export default CalendarPicker;






const CalendarComponent = ({ init, last ,onChangeDate}) => {
  const [year, setYear] = useState((new Date()).getFullYear());
	const [month, setMonth] = useState((new Date()).getMonth());
	const [calendarData, setCalendarData] = useState([]);
  const [yearPicker, setYearPicker] = useState(false);

  useEffect(() => {
    setCalendarData(renderCalendar(month, year));
    return () => {
      //
    }
  }, [month, year])

  let monthlabel = _monthNames["es"][month+1]


  const changeMonth = (m) =>{
    if(month+m > 11 || month+m < 0){
      let mm = month+m > 11 ? 0 :  month+m < 0 ? 11 : month+m;
      let yy = month+m > 11 ? year+1 : month+m < 0 ? year-1 : year;
      setMonth(mm);
      setYear(yy);
    }
    else{
      setMonth(month+m);
    }
  }

  const changeYear = (yy) =>{
    setYear(yy);
    setYearPicker(false);
  }


  const onChange = (day) =>{
    const date = day && (new Date(year,month,day)).getTime();
    //date && onChangeDate(date+60000);
    date && onChangeDate(date);
  }

  
 
  

  return (
    <div style={{marginLeft: 4}}>
        <div  className={"_dsplFlx"} style={styles.date_calendar_header}>
            <div
              onClick={()=>setYearPicker(!yearPicker)}
              size={40}
            >
              <div
               className={"_dsplFlx"}
                style={{padding: 8}}
              >
                <div  style={ styles.date_calendar_header_yearmonth}>
                  {monthlabel}
                </div>
                <div  style={ styles.date_calendar_header_yearmonth}>
                  {year}
                </div>
                <div style={{paddingHorizontal:2}}>
                  <Icon2 name={'arrow_drop_down'} size={24} color={"#8d8d8d"} />
                </div>
              </div>
            </div>
          <div className="flexSpace"/>
          {!yearPicker && (
            <div  className={"_dsplFlx"} >
              <div  style={{borderRadius:50}}>
                <div style={{padding:"0 2px", marginRight: 12}}  onClick={()=>changeMonth(-1)}>
                  <Icon2 name={'arrow_left'} size={24} color={"#444"} />
                </div>
              </div>
            </div>
            )
          }
           
          {!yearPicker?
           <div
            className={"_dsplFlx"}
            >
              <div  style={{borderRadius:50}}>
                <div style={{padding:"0 2px"}}  onClick={()=>changeMonth(1)}>
                  <Icon2 name={'arrow_right'} size={24} color={"#444"} />
                </div>
              </div>
            </div>
          :null}
                    
          </div>
        {!yearPicker?
        <MonthRenderComponent 
          year={year}  
          month={month} 
          calendarData={calendarData} 
          onChange={onChange}
          init={init}
          last={last}
        />
        :
        <YearlistComponent year={year} onChange={changeYear}/> 
      }
    </div>
  )

}





const MonthRenderComponent = ({ year, month,  calendarData, onChange, init, last}) => {

  let dayFactor = 86400000;

  let initSum =  Math.floor(init / dayFactor);
  let lastSum =  Math.floor(last / dayFactor);

  return (
      <>
      <div  className={"_dsplFlx"} style={styles.date_calendar_week_title}>
      {_dayShortNames["en"].map((day, wwInd)=>{
        return (
          <div key={day + "_" +wwInd} style={styles.date_calendar_week_title_day}>
            {day}
          </div>
        )
      })}
      </div>


      <div style={{minHeight: 276}}>
        {calendarData.map((week, wInd)=>{
          return(
            <div key={"week_"+ wInd} 
            className={"_dsplFlx"}
            style={styles.date_calendar_days_title}>
                {week.map((dd, dIndx)=>{
                  let day = dd.day;

                  let dayTime = new Date(year,month,day);
                  
                  let daySum =  Math.floor(dayTime / dayFactor);

                  let isSelected = day && daySum > initSum && 
                  daySum < lastSum;
                  let firstValidDate = day && daySum === initSum;
                  let lastValidDate = day && daySum === lastSum;

                  return (
                    <div
                      key={dIndx + "_day_" + wInd}
                      style={
                        {
                          width:"14%",
                          flexGrow: 1,

                          borderTopRightRadius: day && lastValidDate?20:0,
                          borderBottomRightRadius: day && lastValidDate?20:0,

                          borderTopLeftRadius: day && firstValidDate ?20:0,
                          borderBottomLeftRadius: day && firstValidDate ?20:0,
                          backgroundColor: day && lastValidDate? 
                          activeBckgrnColor: day && firstValidDate ?
                          activeBckgrnColor :isSelected ?
                            activeBckgrnColor : "transparent"
                          
                        }}
                    >
                    <div
                      className={"day_cal"}
                      style={{
                        borderWidth: 2,
                        padding: 11,
                        fontSize: 12,
                        textAlign: "center",
                        fontFamily:  "Google Sans,GoogleSans-Local",
                        fontWeight: dd.isToday ? 600 :  400,
                        backgroundColor: day && lastValidDate? 
                        activeColor: day && firstValidDate ?
                        activeColor : "transparent",
                        borderRadius: dd.isToday ?
                        50:  day && lastValidDate? 
                        50: day && firstValidDate ?
                        50 :2,

                        borderColor: dd.isToday ? activeColor: "transparent",
                        color: (lastValidDate || firstValidDate) ?"#fff": dd.isToday ? activeColor: "#2f2f2f"
                      }}
                      onClick={()=>onChange(day)}
                    >
                      <div  
                        style={{
                          fontSize: 15,
                          textAlign: "center",
                          fontFamily:  "Google Sans,GoogleSans-Local",
                          fontWeight: dd.isToday ? 600 :  400,
                      
                          borderRadius: dd.isToday ?20: 0,

                          borderColor: dd.isToday ? activeColor: "transparent",
                          color: (lastValidDate || firstValidDate) ?"#fff": dd.isToday ? activeColor: "#2f2f2f"
                        }}
                      >
                        {day}
                      </div>
                    </div>
                    </div>
                  )
                })}
            </div>
        )})}
      </div>
    </>
  )

}







const YearlistComponent = ({ year, onChange}) => {
  let yearList = Array.from({length: 149}, (_, i) => i + ((new Date()).getFullYear() - 100));

  let scrollRef = useRef();

  //const [size, onLayout] = useComponentSize();
  //console.log(size)


  
  useEffect(() => {
    setTimeout(() => {
      let diff = (year - (new Date()).getFullYear()) / 3;
      let halfFactor = (16 + diff)* 41.6;
      scrollRef.current.scrollTo({ x: halfFactor, y: 0, animated: true })
    }, 20);
    return () => {
      //
    }
  }, [year])

  
  return (
        <div>
          <div
            ref={scrollRef}
            //contentInsetAdjustmentBehavior="automatic"
            style={{ maxHeight: "70vh"}}
            className={"scrollV"}
            >
            <div  
              className={"_dsplFlx"}  
              style={{
                flexWrap: "wrap",
                justifyContent: "space-around"
              }}
            >
            {yearList.map(yy=>{
              return (
                <div
                  //onLayout={onLayout}
                  key={yy}
                  style={styles.year_tag}
                  onClick={()=>onChange(yy)}
                >
                  <div
                    style={{
                      marginHorizontal: 6,
                      borderWidth: 2,
                      borderColor: yy===year ? activeColor: "transparent",
                      backgroundColor:  yy===year ? activeBckgrnColor : "transparent",
                      borderRadius: yy===year ? 20: 0 
                      
                    }}
                  >
                    <div  
                      style={{
                        padding: 2,
                        paddingVertical: 6,
                        color: yy===year ? activeColor : "#8a8a8a",
                        
                        fontSize: 13,
                        fontFamily:yy===year ?  "Google Sans,GoogleSans-Local" :  "Google Sans,GoogleSans-Local",
                        textAlign: "center"
                      }}
                    >
                      {yy}
                    </div>
                  </div>
                </div>
              )
            })}
            </div>
          </div>
        </div>
  )
}






